<template>
  <el-dialog v-dialogDrag :title="title" :visible.sync="dialog" :close-on-click-modal=false append-to-body width="800px">
    <el-form ref="form" :rules="rules" :model="form" label-width="90px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.id" disabled maxlength="15" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="realName" :label-width="formLabelWidth">
            <el-input v-model="form.realName" maxlength="15" clearable></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="" prop="school" :label-width="formLabelWidth">
            <el-input v-model="form.school" disabled maxlength="15" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.sex" style="width: 100%">
              <el-option v-for="item in sexArr" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="" prop="profession" :label-width="formLabelWidth">
            <el-input v-model="form.profession" disabled readonly maxlength="15" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="grade" :label-width="formLabelWidth">
            <el-input v-model="form.grade" disabled maxlength="15" readonly clearable></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="" prop="email" :label-width="formLabelWidth">
            <el-input v-model="form.email" maxlength="30" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="" prop="phone" :label-width="formLabelWidth">
            <el-input v-model="form.phone" maxlength="15" clearable></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer">
      <el-button type="primary" @click="click('form')" size="small"></el-button>
      <el-button @click="cancel" size="small"></el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "editInfo-model",
  data() {
    let validEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!value) {
        return callback(new Error(''))
      } else {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }
    };
    let validPhone = (rule, value, callback) => {
      const phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
      if (!value) {
        return callback(new Error(''))
      } else {
        if (!Number.isInteger(+value)) {
          callback(new Error(''))
        } else {
          if (phoneReg.test(value)) {
            callback()
          } else {
            callback(new Error(''))
          }
        }
      }
    };
    return {
      formLabelWidth: '100px',
      dialog: false,
      title: '',
      sexArr: [{
        value: 0,
        label: ''
      }, {
        value: 1,
        label: ''
      }],
      form: {
        username: '',
        realName: '',
        sex: '',
        school: '',
        id: '',
        email: '',
        profession: '',
        phone: '',
        code: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { max: 15, message: '15', trigger: 'blur' }
        ],
        realName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        email: [
          {required: true, validator: validEmail, trigger: 'blur'},
        ],
        phone: [
          {required: true, validator: validPhone, trigger: 'blur'},
        ],
      }
    }
  },
  methods: {
    init (obj) {
      this.form = obj;
      this.dialog = true;
    },
    click (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
            this.editMethod();
        } else {
          return false;
        }
      })
    },
    cancel () {
      this.dialog = false;
    },
    editMethod () {
      this.axios.put(
        '/admin/student', this.form).then(() => {
        this.$message.success({
          message: ''
        });
        this.$store.commit('SAVE_USERINFO', this.form);
        this.$emit('refresh');
        this.dialog = false;
      }).catch(() => {
        this.$message.error({
          message: ''
        });
        this.dialog = false;
      });
    }
  }
}
</script>

