<template>
  <el-dialog
    v-dialogDrag
    title=""
    :visible.sync="dialog"
    :close-on-click-modal="false"
    append-to-body
    class="type-one-dialog"
    width="500px"
  >
    <el-form ref="form" :model="form" :rules="rules">
      <el-form-item label="" prop="email" :label-width="formLabelWidth">
        <el-input
          type="text"
          v-model="form.email"
          clearable
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="code" :label-width="formLabelWidth">
        <span>
          <el-input
            class="code"
            type="text"
            v-model="form.code"
            maxlength="6"
            clearable
            placeholder=""
          ></el-input>
          <el-button type="primary" ref="codebutton" :disabled="disabled" :loading="disabled" @click="getcode">{{
            state
          }}</el-button>
        </span>
      </el-form-item>
      <el-form-item label="" prop="newPass" :label-width="formLabelWidth">
        <el-input
          type="password"
          show-password
          v-model="form.newPass"
          maxlength="15"
          clearable
          placeholder=""
        ></el-input>
      </el-form-item>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button type="primary" @click="click"></el-button>
      <el-button @click="cancel"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import Md5 from "js-md5";
export default {
  name: "forgetpass",
  data() {
    return {
      formLabelWidth: "120px",
      dialog: false,
      disabled: false,
      form: {
        email: "",
        code: "",
        newPass: ""
      },
      pushform:{
        code: "",
        newPass: ""
      },
      state: "",
      rules: {
        email: [{ required: true, message: "", trigger: "blur" }],
        code: [{ required: true, message: "", trigger: "blur" }],
        newPass: [
          { required: true, message: "", trigger: "blur" }],
      },
    };
  },
  methods: {
    init() {
      this.dialog = true;
      this.clearForm();
    },
    clearForm() {
      this.form = {
        email:"",
        code: "",
        newPass: ""
      };
    },
    cancel() {
      this.dialog = false;
    },
    getcode() {
      if(this.form.email === ""){
        this.$message.error("!");
        return;
      }
      let time = 60;
      let timer = setInterval(() => {
        if (time == 0) {
          clearInterval(timer);
          this.state = "";
          this.disabled = false;
        } else {
          this.disabled = true;
          this.state = time + "";
          time--;
        }
      }, 1000);
      this.axios.get("/user/getcode/"+this.form.email);
    },
    click() {
      this.$refs["form"].validate(async (valid) => {
        if (valid) {
          this.pushform.code = this.form.code;
          this.pushform.newPass = Md5(this.form.newPass);
          const { data: res } = await this.axios.put("/user/forgotPass", this.pushform);
          if (res.code === 200) {
            this.$message.success({
              message: "",
            });
            this.$router.push("/login");
            // 
            localStorage.clear();
            this.dialog = false;
          } else {
            this.$message.error(res.msg);
          }
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style scoped>
.code {
  width: 58%;
  margin-right: 7px;
}
</style>
